/* App.css */
body {
  font-family: "Times New Roman", Times, serif;
  background-color: #000; /* Black background */
  color: #ff0000; /* Red text */
  display: flex; /* Use flexbox */
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  height: 100vh; /* Set height to 100% of viewport height */
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #000; /* Black navbar background */
}


.navbar-left h1 {
  margin: 0;
}


.navbar-right p {
  margin: 0;
}

.card-container {
  display: flex;
  justify-content: space-around;
  gap: 25px; /* Add space between cards */
  margin-top: 50px; /* Top margin */
}

.card-block {
  width: 100px;
  height: 150px;
  perspective: 1000px;
  cursor: pointer;
  background-color: #ff0000; /* Red card background */
  border-radius: 15px;
}

.card-block.flipped .card-front {
  transform: rotateY(-180deg);
}

.card-block.flipped .card-back {
  transform: rotateY(0deg);
}

.card-front,
.card-back {
  width: 100%;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
  border-radius: 15px;
}

.card-front {
  background-color: #ff0000;
  border: 1px solid #ffb330;
}

.card-back {
  background-color: #333;
  border: 1px solid #111;
  transform: rotateY(180deg);
}

.toast {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #ff0000; /* Red toast background */
  color: #fff; /* White text */
  padding: 10px 20px;
  border-radius: 5px;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.toast.show {
  opacity: 1;
}

/* Media query for mobile view */
@media (max-width: 768px) {
  .navbar-left h1 {
    font-size: 27px; /* Increase the font size to your desired value */
  }
  
  .navbar-right p {
    font-size: 20px;
  }

  .card-container {
    flex-wrap: wrap;
  }
  
  .card-block {
    margin: 10px;
  }
}

/* Media query for laptop view */
@media (min-width: 769px) {
  .navbar-left h1 {
    font-size: 35px; /* Increase the font size to your desired value */
  }
  
  .navbar-right p {
    font-size: 27px;
  }

  .card-block {
    height: 380px;
    width: 200px; /* Increased width for laptop view */
  }
}
